<script>
    export let caseTitle;
    export let caseRole;
    export let caseDescription;
    export let caseExecution;
    export let caseLink;
    export let caseImage;
    export let caseTech;
    export let orange;
    export let blue;
    export let red;
    export let green;
    export let purple;
</script>

<article class:red class:blue class:green class:orange class:purple>
    {#if caseLink}
        <a href="{caseLink}" target="_blank">Link</a>
    {:else}
        <span class="nolink">No link available</span>
    {/if}
    <h2>{caseTitle}</h2>
    <h3>{caseRole}</h3>
    <img src="{caseImage}" alt="{caseTitle + ' case image'}" />
    <h4>What?</h4>
    <p>{caseDescription}</p>
    <h4>How?</h4>
    <p>{caseExecution}</p>
    <h4>Tech</h4>
    <p>{caseTech}</p>
</article>

<style>
    article {
        padding: 30px;
        box-shadow: 3px 3px 13px 13px rgba(0, 0, 0, 0.1);
        margin-bottom: 50px;
    }
    article:before {
        content: '';
        position: absolute;
        width: calc(100% - 65px);
        height: 4px;
        z-index: 99;
        margin-top: -30px;
        margin-left: -30px;
    }
    .orange:before {
        background: linear-gradient(to left, #f7ff00, #e58a00);
    }
    .blue:before {
        background: linear-gradient(to left, #1bffff, #1153c7);
    }
    .green:before {
        background: linear-gradient(to left, #fcee21, #489b6f);
    }
    .red:before {
        background: linear-gradient(to left, #ff9b00, #ff005d);
    }
    .purple:before {
        background: linear-gradient(to left, #ed1e79, #8b1ed3);
    }
    img {
        width: 100%;
        margin: 5px 0;
    }
    a, .nolink {
        float: right;
    }
    h4 {
        font-weight: bold;
    }
</style>